<template>
    <div class="search">
        <el-form :inline="true" :model="formInline">
            <el-form-item label="标题" prop="searchText">
                <el-input placeholder="请输入要搜索的标题" style="width:400px" v-model="searchText"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="info">查询</el-button>
            </el-form-item>
            <el-form-item>
                <el-button @click="showAddItem" type="primary">添加</el-button>
            </el-form-item>
            <el-form-item>
                <el-button @click="delSelectItems" type="danger">批量删除</el-button>
            </el-form-item>
        </el-form>
        <el-dialog title="新建项目" :visible.sync="showAddItemDialog" center width="40%">
            <el-form :model="formDate" :rules="rules">
                <el-form-item label="图片" prop="addPicture">
                    <el-upload list-type="picture-card">
                        <i class="el-icon-plus"></i>
                    </el-upload>
                </el-form-item>
                <el-form-item label="标题" prop="addTitle">
                    <el-input placeholder="请输入标题" style="width:60%" clearable v-model="formDate.title"></el-input>
                </el-form-item>
                <el-form-item label="描述" prop="addDesc">
                    <el-input placeholder="请输入描述内容" style="width:60%" v-model="formDate.descc" type="textarea" resize="none" rows="4"></el-input>
                </el-form-item>
            </el-form>
            <footer class="footer">
                <el-button @click="addItem" size="small" type="primary" plain>立即添加</el-button>
                <el-button @click="cancel" size="small" type="danger" plain>取消</el-button>
            </footer>
        </el-dialog>
    </div>
</template>
<script>
export default {
    data() {
        return {
            searchText: '',
            showAddItemDialog: false,
            formDate: {
                title: '',
                desc: ''
            },
            rules: {
                searchText: [
                    { required: true, message: '请输入搜索内容', trigger: 'blur' },
                    { min: 1, max: 12, message: '长度在 1 到 12 个字符', trigger: 'blur' }
                ],
                addTitle: [
                    { required: true, message: '请输入项目标题', trigger: 'blur' },
                    { min: 1, max: 12, message: '长度在 1 到 12个字符', trigger: 'blur' }
                ],
                addDesc: [
                    { required: true, message: '请输入项目的描述', trigger: 'blur' },
                    { min: 10, max: 150, message: '长度在 10 到 150 个字符', trigger: 'blur' }
                ],
                addPicture: [
                    { required: true, message: '请输入活动名称', trigger: 'blur' },
                ]
            }
        }
    },
    methods: {
        showAddItem() {
            this.showAddItemDialog = true
        },
        addItem() {

        },
        cancel() {
            this.showAddItemDialog = false
        },
        delSelectItems() {
            
        }
    }
}
</script>
<style scoped>
    .search {
        align-self: center;
        margin: 0 auto;
    }
    .footer {
        display: flex;
        display: -webkit-flex;
        display: -ms-flexbox;
        justify-content: center;  
    }
</style>





